.title {
  border: 1px solid grey;
  box-shadow: 5px 10px #888888;
  width: 100%;
  height: fit-content;
  padding: 10px;
}
.heading {
  display: flex;
  justify-content: space-between;
}
.head_content{
  display: flex;
  justify-content: space-evenly;
}
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 10px;
}

.card {
  border: 1px solid black;
  width: 40%;
  height:auto;
  margin-bottom: 10px;
  display: flex;
  flex-direction: column;
  background-color: #fff;
  border-radius: 0.3rem;
}

.card > .card_title {
  height: 8vh;
  /* border: 1px solid black; */
  display: flex;
  justify-content: space-between;
  padding: 0px 5px;
}
.name_section {
  font-size: larger;
  position: relative;
  bottom: 30%;
}
.name_section > h1{
  font-size: 1rem;
}
.name_section > h1 > span {
  font-weight: bold;
  opacity: 0.5;
}
.dots {
  font-size: 2rem;
}
.card_image {
  border-top: 1px solid;
  border-bottom: 1px solid;
}
.icons_section {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0px 5px;
  width: fit-content;
  height: auto;
}
.icons_section > p {
  opacity: 0.5;
  position: relative;
  font-weight: bold;
  bottom: 0.5rem;
}
#likes {
  opacity: 0.8;
  position: relative;
  left: 1.5rem;
  bottom: 1rem;
}
.caption{
  text-align: left;
  padding: 0px 2rem;
  position: relative;
  bottom: 3rem;

}
.Click_button{
  height: 40px;  
  width: 150px;
  border-radius: 20px;
  padding: 2px;
  background-color: #fff;
  position: relative;
  right: 0.8rem;
  top:2rem;
  
 }